import { useState, useEffect } from 'react';
import axios from 'axios';
import { useHistory } from 'react-router-dom';

const Home = () => {
  const [flights, setFlights] = useState([]);
  const [flightDetail, setFlightDetail] = useState({})
  const getListTeam = async () => {
    try {
      const response = await axios.get('https://flight-data4.p.rapidapi.com/get_area_flights?latitude=1.367540&longitude=103.816089', {
        headers: {
          "x-rapidapi-key": "6f4d290879mshb37546c9642c606p190b88jsnd90c014366b4",
          "x-rapidapi-host": "flight-data4.p.rapidapi.com",
          "useQueryString": true
        }
      });
      setFlights(response?.data?.flights)
    } catch (err) {
      console.log(err)
    }
  }
  const getDetail = async (flight) => {
    try {
      const response = await axios.get(`https://flight-data4.p.rapidapi.com/get_flight_info?flight=${flight}`, {
        headers: {
          "x-rapidapi-key": "6f4d290879mshb37546c9642c606p190b88jsnd90c014366b4",
          "x-rapidapi-host": "flight-data4.p.rapidapi.com",
          "useQueryString": true
        }
      });
      setFlightDetail(response?.data?.[flight])
    } catch (err) {
      console.log(err)
    }
  }
  useEffect(() => {
    getListTeam()
  }, [])

  return (
    <>
      <div style={{
        display: 'grid',
        fontSize: '24px',
        gridTemplateColumns: '15rem auto',
        columnGap: '1.5rem',
        rowGap: '1.5rem',
      }}>
        <div style={{
          display: 'flex',
          flexDirection: 'column',
          rowGap: '10px',
          height: '100vh',
          overflowY: 'scroll',
          backgroundColor: '#f2380a'
        }}>
          {flights.map((item, index) => {
            return (
              <div style={{
                padding: '4px 8px',
                borderRadius: '8px',
                color: 'white',
                cursor: 'pointer',
                borderBottom: '2px solid white'
              }}
                onClick={() => { getDetail(item?.flight) }}
              >
                {item?.flight}
              </div>
            )
          })}
        </div>

        <div style={{ display: 'flex', flexDirection: 'column', rowGap: '8px', fontSize: '32px',justifyContent:'center' }}>
          {Object.keys(flightDetail).length ?
            <>
              <div>
                {flightDetail?.flight?.iata}-{flightDetail?.airline?.name}
              </div>
              <div>
                Aircraft : {flightDetail?.aircraft?.desc}, {flightDetail?.aircraft?.ff}
              </div>
              <div>
                Departure : {flightDetail?.arr_airport?.city},{flightDetail?.arr_airport?.name}
              </div>
              <div>
                Arrival : {flightDetail?.dep_airport?.city},{flightDetail?.dep_airport?.name}
              </div>
            </>

            : 'Please Select Flight'}

        </div>
      </div>
    </>)
}

export default Home;